function run() {
  var v = {};
  switch (__OS) {
    case "IOS":
      v = { android: require("./ios") }
      break;
    case "WEB":
      v = { android: require("./web") }
      break;
    default:
      v = { android: require("./android") }
  }


  return v;
}

module.exports = run();



